	import SongFooter from "../../components/uni-music/SongFooter"
    export default {
		components:{
			SongFooter
		},
        data() {
            return {
                title: 'list-triplex-row',
                banner: {},
                htmlString: ""
            }
        },
        onShareAppMessage() {
            return {
                title: this.banner.title,
                path: '/pages/template/list2detail-detail/list2detail-detail?detailDate=' + JSON.stringify(this.banner)
            }
        },
        onLoad(event) {
            // 目前在某些平台参数会被主动 decode，暂时这样处理。
            try {
                this.banner = JSON.parse(decodeURIComponent(event.detailDate));
            } catch (error) {
                this.banner = JSON.parse(event.detailDate);
            }
            this.getDetail();
            uni.setNavigationBarTitle({
                title: this.banner.title
            });
        },
        methods: {
            getDetail() {
                uni.request({
                    url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + this.banner.post_id,
                    success: (data) => {
                        if (data.statusCode == 200) {
							alert(this.banner.post_id);
							console.log(data);
                            this.htmlString = data.data.content.replace(/\\/g, "").replace(/<img/g,
                                "<img style=\"display:none;\"");
                        }
                    },
                    fail: () => {
                        console.log('fail');
                    }
                })
            }
        }
    }
